// src/components/Header.js
import React from "react";
import { Link, useLocation } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import logo from "../assets/k_craftbeer_logo.png";

const Header = () => {
  const location = useLocation();

  return (
    <nav className="navbar navbar-expand-lg bg-white border-bottom shadow-sm sticky-top">
      <div className="container-fluid px-md-4 px-lg-4">
        <Link className="navbar-brand fw-bold text-warning fs-3" to="/">
          <img
            src={logo}
            alt="K-CraftBeer 로고"
            style={{ height: "80px", marginRight: "10px" }}
          />
          <span className="fw-bold text warning fs-2">K-CraftBeer</span>
        </Link>

        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav ms-auto">
            {[
              { path: "/", label: "홈" },
              { path: "/manage", label: "관리" },
              { path: "/about", label: "소개" },
              { path: "/community", label: "커뮤니티" },
            ].map(({ path, label }) => (
              <li className="nav-item" key={path}>
                <Link
                  to={path}
                  className={`nav-link px-3 ${
                    location.pathname === path
                      ? "active text-warning fw-semibold"
                      : "text-dark"
                  }`}
                >
                  {label}
                </Link>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Header;
