import { Marker, InfoWindow } from "@react-google-maps/api";
import { useState } from "react";

// 마커 + 정보창(InfoWindow)
const BreweryMarker = ({ brewery }) => {
  const [open, setOpen] = useState(false); // InfoWindow 열림 여부. false 상태가 닫힘 상태
  const { name, location, image, homepage } = brewery;

  const customIcon = {
    url: "/images/beer_marker2.png",
    scaledSize: { width: 50, height: 50 },
  };

  return (
    <>
      <Marker
        position={{ lat: location.y, lng: location.x }}
        icon={customIcon}
        onClick={() => setOpen(true)} // 클릭하면 InfoWindow 열림.
      />
      {open && (
        <InfoWindow
          position={{ lat: location.y, lng: location.x }}
          onCloseClick={() => setOpen(false)} // 닫기 버튼 누르면 InfoWindow 닫힘
        >
          <div>
            <h4>{name}</h4>
            <img src={image} alt={name} style={{ width: "120px" }} />
            <p>
              <a href={homepage} target="_blank" rel="noreferrer">
                홈페이지
              </a>
            </p>
          </div>
        </InfoWindow>
      )}
    </>
  );
};

export default BreweryMarker;

/*
Marker : 지도 위에 마커(핀)을 표시하는 컴포넌트
InfoWindow : 마커 클릭시 나타나는 저옵창. 브루어리 이름, 이미지, 홈페이지 링크 포함
*/
