import React, { useEffect, useState } from "react";
import {
  getBreweryList,
  createBrewery,
  updateBrewery,
  deleteBrewery,
} from "../api/breweryApi";
import BreweryForm from "../components/BreweryForm";
import BreweryList from "../components/BreweryList";

const ManagePage = () => {
  const [breweries, setBreweries] = useState([]);
  const [form, setForm] = useState({
    name: "",
    image: "",
    homepage: "",
    locationX: "",
    locationY: "",
  });
  const [editingId, setEditingId] = useState(null);

  useEffect(() => {
    loadBreweryList();
  }, []);

  const loadBreweryList = async () => {
    try {
      const res = await getBreweryList();
      setBreweries(res.data);
    } catch (err) {
      console.error("양조장 목록 조회 실패:", err);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async () => {
    try {
      const locationX = parseFloat(form.locationX);
      const locationY = parseFloat(form.locationY);
      if (isNaN(locationX) || isNaN(locationY)) {
        alert("위도와 경도를 숫자로 정확히 입력해주세요.");
        return;
      }

      const location = {
        x: locationX,
        y: locationY,
        type: "Point",
        coordinates: [locationX, locationY],
      };

      const body = {
        name: form.name,
        image: form.image,
        homepage: form.homepage,
        location,
      };

      if (editingId) {
        await updateBrewery(editingId, body);
      } else {
        await createBrewery(body);
      }

      await loadBreweryList();
      setForm({
        name: "",
        image: "",
        homepage: "",
        locationX: "",
        locationY: "",
      });
      setEditingId(null);
    } catch (err) {
      console.error("저장 실패:", err);
    }
  };

  const handleEdit = (brewery) => {
    setForm({
      name: brewery.name || "",
      image: brewery.image || "",
      homepage: brewery.homepage || "",
      locationX: brewery.location?.x || "",
      locationY: brewery.location?.y || "",
    });
    setEditingId(brewery.id);
  };

  const handleDelete = async (id) => {
    if (!window.confirm("정말 삭제하시겠습니까?")) return;
    try {
      await deleteBrewery(id);
      await loadBreweryList();
    } catch (err) {
      console.error("삭제 실패:", err);
    }
  };

  return (
    <div className="container mt-5">
      <h2 className="text-left text-warning mb-4">
        <strong>🍺 수제 맥주 양조장 지도 관리</strong>
      </h2>

      <BreweryForm
        form={form}
        onChange={handleChange}
        onSubmit={handleSubmit}
        editingId={editingId}
      />

      <BreweryList
        breweries={breweries}
        onEdit={handleEdit}
        onDelete={handleDelete}
      />
    </div>
  );
};

export default ManagePage;
