import axios from "axios";

const API_BASE = "http://localhost:9000";

// 전체 목록 조회 (지도용)
export const getAllBreweries = () => {
  // axios.get() : 해당 url로 HTTP GET 요청
  return axios.get(`${API_BASE}/brewery/api`);
};

// 전체 목록 조회 (관리 페이지용)
export const getBreweryList = () => {
  return axios.get(`${API_BASE}/brewery`);
};

// 단일 양조장 조회 (선택적 사용)
export const getBreweryById = (id) => {
  return axios.get(`${API_BASE}/brewery/${id}`);
};

// 양조장 추가
export const createBrewery = (brewery) => {
  return axios.post(`${API_BASE}/brewery`, brewery);
};

// 양조장 수정
export const updateBrewery = (id, brewery) => {
  return axios.put(`${API_BASE}/brewery/${id}`, brewery);
};

// 양조장 삭제
export const deleteBrewery = (id) => {
  return axios.delete(`${API_BASE}/brewery/${id}`);
};
