import React from "react";

const BreweryForm = ({ form, onChange, onSubmit, editingId }) => (
  <div className="card mb-5">
    <div className="card-body">
      <h4 className="card-title mb-3">
        {editingId ? "양조장 수정" : "양조장 추가"}
      </h4>

      <div className="row g-2">
        <div className="col-md-6">
          <input
            name="name"
            className="form-control"
            placeholder="이름"
            value={form.name}
            onChange={onChange}
          />
        </div>
        <div className="col-md-6">
          <input
            name="image"
            className="form-control"
            placeholder="이미지 URL"
            value={form.image}
            onChange={onChange}
          />
        </div>
        <div className="col-md-12">
          <input
            name="homepage"
            className="form-control"
            placeholder="홈페이지 URL"
            value={form.homepage}
            onChange={onChange}
          />
        </div>
        <div className="col-md-6">
          <input
            name="locationY"
            className="form-control"
            placeholder="위도 (latitude)"
            value={form.locationY}
            onChange={onChange}
          />
        </div>
        <div className="col-md-6">
          <input
            name="locationX"
            className="form-control"
            placeholder="경도 (longitude)"
            value={form.locationX}
            onChange={onChange}
          />
        </div>
      </div>

      <button onClick={onSubmit} className="btn btn-warning mt-3 w-100">
        {editingId ? "수정 완료" : "등록"}
      </button>
    </div>
  </div>
);

export default BreweryForm;
